.mobile-menu {
  width: 100vw;
  height: 100vh;
  position: fixed;
  display: none;
  background-color: rgba(0, 0, 0, 0.3);
  left: 0;
  top: 0;
  z-index: 999 !important;
  opacity: 0;
  box-shadow: 0px 20px 80px rgba(0, 0, 0, 0.3);
  transition: all 0.3 ease;
  transform: translateX(-100vw);
}

.mobile-menu-container {
  width: 60vw;
  height: 100vh;
  background-color: #342864;
  padding: 2rem;
}

.mobile-menu.active {
  opacity: 1;
  transform: translateX(0);
}

.mobile-menu h1 {
  margin-bottom: 3rem;
}

.mobile-menu ul {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  list-style: none; 
}

@media (max-width: 769px) {
  .mobile-menu {
    display: block;
  }

  .logo {
    font-size: 20px;
  }

}


@media (max-width: 320px){
  .mobile-menu-container{
     height: 80%;
  }

  .mobile-menu{
    display: block;
    justify-content: end;
  }
}